@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600&display=swap);
body {
  font-family: Inter, serif;
}

code {
  font-family: Inter, serif;
}

.main-screen{
    height: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.add-background{
    background: linear-gradient(to bottom right, var(--white-100) 50%, var(--orange-5) 50%);
}

.main-image-container{
    height: 100%;
    margin: 0 25%;

    background-image: url(/Millionaire/static/media/hand.bfff441f.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

.main-text-container{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    height: 100%;
}

@media only screen and (max-width: 600px) {
    .main-screen{
        grid-template-columns: 1fr;
        grid-template-rows: repeat(2, 1fr);
    }

    .main-text-container{
        align-items: center;
        flex-direction: column;
        text-align: center;
        justify-content: stretch;
        margin: 15% 0;
    }

    .main-image-container{
        margin: 15% 15% 0;
    }

    .game-button{
        margin-top: 10%;
    }
}
.game-container{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    height: 100%;
}

.mobile-buttons{
    display: none;
    border: none;
    background: transparent;
    cursor: pointer;
}

.mobile-buttons:hover{
    outline: none;
}

.mobile-buttons:focus{
    outline: none;
}

.image-close{
    background-image: url(data:image/png;base64,);
}

.image-menu{
    background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAOCAYAAAAmL5yKAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAzSURBVHgB7dGxEQAgCATB/2+DRPsvSjvBHjCAYdj8oqPZPiQWAtxxJSHsp+2Ec6GA/AsPvy0MxEjzsukAAAAASUVORK5CYII=);
}


@media only screen and (max-width: 900px) {
    .game-container{
        background-color: var(--black-5);
        grid-template-columns: 1fr;
        padding: 4rem 0 1.5rem;
        box-sizing: border-box;
    }

    .mobile-buttons{
        position: absolute;
        display: block;
        height: 15px;
        width: 16px;
        margin: 20px;
        top: 0;
        right: 0;

        background-color: transparent;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: right;
    }
}
.score-container{
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
}


.button{
    background: var(--orange-100);
    text-align: center;
    height: var(--buttop-height);
    width: var(--buttop-width);
    color: var(--white-100);

    cursor: pointer;
    border: none;
    border-radius: var(--border-radius);
}

.button:hover{
    outline: none;
    background: var(--orange-80);
}

.button:focus{
    outline: none;
    background: var(--orange-120);
}
.option-container{
    cursor: pointer;
    background: transparent;
    border: none;
    margin: 1rem 0;

    --height: var(--option-height);
    --width: var(--option-width);
    --margin-size: 5%;
    --border-color: var(--orange-100);
    --var-background-image-option: url(data:image/png;base64,);

    -webkit-tap-highlight-color: transparent;
}

.option-container.no-hover{
    pointer-events: none;
}

.option{
    display: flex;
    justify-content: start;
    align-items: center;
    justify-self: center;
    box-sizing: border-box;

}


.option-text-container{
    margin: 0 32px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0;

}

.option-text{
    flex: none;
    flex-grow: 0;
    margin: 0 8px;
}

.option-text.option-label{
    width: 100%;
    margin: 0;
    padding: 0;
    text-align: start;
}

.option-id{
    color: var(--orange-80);
}

.option-label{
    font-weight: var(--light-font-weight);
    color: var(--black-100)
}


.option-container.can-hover:hover{
    outline: none;
    cursor: pointer;
    --border-color: var(--orange-100);
    --var-background-image-option: url(data:image/png;base64,);
}

.option-container.no-hover:hover{
    outline: none;
    cursor: default;
}


.option-container:focus{
    outline: none;
}

.option-type-default{
    --border-color: var(--black-40);
    --var-background-image-option: url(data:image/png;base64,);
}

.option-type-correct{
    --border-color: var(--green-100);
    --var-background-image-option: url(data:image/png;base64,);
}
.option-type-incorrect{
    --border-color: var(--red-100);
    --var-background-image-option: url(data:image/png;base64,);
}
.option-type-selected{
    --border-color: var(--orange-100);
    --var-background-image-option: url(data:image/png;base64,);
}


@media only screen and (max-width: 600px) {

    .option-container {
        --var-background-image-option: url(data:image/png;base64,);
    }

    .option-type-correct{
        --var-background-image-option: url(data:image/png;base64,);
    }
    .option-type-incorrect{
        --var-background-image-option: url(data:image/png;base64,);
    }
    .option-type-selected{
        --var-background-image-option: url(data:image/png;base64,);
    }

    .option-container{
        margin: 0.25rem 0;
        justify-content: space-between;
    }
}



.step-container{
    --height: var(--prize-height);
    --width: var(--prize-width);
    --margin-size: 15%;
    --color: var(--black-100);
    --border-color: var(--black-40);
    --var-background-image-option: url(data:image/png;base64,);
    margin: 0.25rem 0;
}

.step{
    font-weight: var(--light-font-weight);
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color);
}

.step:after{
    -webkit-transform: none !important;
            transform: none !important;
}


.step-type-current{
    --color: var(--orange-100);
    --border-color: var(--orange-100);
    --var-background-image-option: url(data:image/png;base64,);
}

.step-type-prev{
    --color: var(--black-40);
}


@media only screen and (max-width: 600px) {

    .step-container {
        --var-background-image-option: url(data:image/png;base64,);
    }

    .step-container.step-type-current{
        --var-background-image-option: url(data:image/png;base64,) !important;
    }
}



.act-container{
    background: var(--black-5);
    padding: 4rem 3rem;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    grid-column: span 3;
}

.act-container .game-label{
    color: var(--black-100);
    max-width: 39rem;
}

.options-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
}

@media only screen and (max-width: 600px) {
    .act-container{
        display: grid;
        grid-template-rows: 1fr 1fr;
        padding: 0;
    }

    .options-container{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
    }

    .act-container .game-label{
        display: flex;
        text-align: center;
        align-items: center;
        padding: 1rem;
    }
}
html {
  --orange-120: #E87928;
  --orange-100: #FF8B37;
  --orange-80: #FFAC70;
  --orange-5: #FFF3EB;

  --green-100: #47D867;
  --green-5: #E6FAEA;

  --red-100: #EC6259;
  --red-5: #FDEEED;

  --black-100: #1C1C21;
  --black-40: #D0D0D8;
  --black-5: #F5F5F7;

  --white-100: #ffffff;

  --default-font-size: 20px;
  --default-font-weight: 600;
  --light-font-weight: normal;

  --question-font-size: 32px;
  --headline-font-size: 56px;

  --border-radius: 12px;
  --buttop-height: 64px;
  --buttop-width: 296px;

  --option-height: 72px;
  --option-width: 373px;

  --prize-height: 40px;
  --prize-width: 240px;
}

@media only screen and (max-width: 1100px) {
  html {
    --default-font-size: 16px;
    --question-font-size: 24px;
    --headline-font-size: 44px;
    --border-radius: 10px;

    --buttop-height: 54px;
    --buttop-width: 292px;

    --option-height: 64px;
    --option-width: 260px;

    --prize-height: 32px;
    --prize-width: 220px;
  }
}

@media only screen and (max-width: 600px) {
  html {
    --default-font-size: 14px;
    --question-font-size: 18px;
    --headline-font-size: 32px;
    --border-radius: 8px;

    --buttop-height: 48px;
    --buttop-width: 288px;

    --option-height: 56px;
    --option-width: 288px;

    --prize-height: 32px;
    --prize-width: 240px;
  }
}

* {
  font-family: Inter, serif;
  font-style: normal;
  font-weight: 600;
  font-weight: var(--default-font-weight);
  font-size: 20px;
  font-size: var(--default-font-size);
  line-height: 116%;

  color: #C4C4C4;
}

body{
  margin: 0;
}

.App{
  height: 100vh;
}


.image-base{
  height: var(--height);
  width: var(--width);

  position: absolute;
  top: 0;
  left: calc(50% - var(--width)/2);

  z-index: 10;
  border: none;
  padding: 0;
  background-color: transparent;
  background-image: var(--var-background-image-option);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

