.line-container{
  position: relative;
  padding: 0 var(--margin-size);
  height: var(--height);
}

.line-container:after{
  position: absolute;
  content: "";
  height: 1px;
  width: 100%;
  top: 50%;
  left: 0;
  border-top: 1px solid  var(--border-color);
}

.game-header{
  font-size: 56px;
  font-size: var(--headline-font-size);
  color: #1C1C21;
  color: var(--black-100);
  margin: 0 0 64px;
}

.game-label{
  font-size: 32px;
  font-size: var(--question-font-size);
  margin: 0;
}





